import { Injectable } from '@angular/core';
import { from } from 'rxjs';
import {LoginI} from 'src/models/login.interface';
import {ResponseI} from 'src/models/response.interface';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  url:string = "http://localhost:8080/Poryectos_TV-0.1/";

  constructor(private http:HttpClient) { }

  loginByUser(form:LoginI):Observable<ResponseI>{
    let direcction = this.url + "inicioSesion";

    return this.http.post<ResponseI>(direcction,form); 
  }

}
